import React from 'react'
import { Link } from 'react-router-dom';

import { Button, Result } from 'antd'

function RegisterWelcomePage() {
  return (
    <Result
      status="success"
      title="  !"
      subTitle="      ."
      extra={[
        <Button type="primary" key="login" size="large">
          <Link to="/login"></Link>
        </Button>,
      ]}
    />
  )
}

export { RegisterWelcomePage }