export const inputRules = {
    username,
    nickname,
    password,
    passwordRe,
    geekusername,
}

function required() {
    return { required: true, message: ' .' }
}

function email() {
    return { type: 'email', message: '  .' }
}

function min(val) {
    return { min: val, message: ` ${val}  .` }
}

function max(val) {
    return { max: val, message: ` ${val} .` }
}

const equalPassword = ({ getFieldValue: any }) => ({
    validator(rule: any, value: string) {
        if (!value || getFieldValue('password') == value) {
            return Promise.resolve()
        }
        return Promise.reject('   .')
    }
})

const noWhitespace = () => ({
    validator(rule: any, value: string) {
        if (!value || value.indexOf(' ') >= 0) {
            return Promise.reject('   .')
        }
        return Promise.resolve()
    }
})


function username() {
    return [ required(), email(), max(200), noWhitespace ]
}

function nickname() {
    return [ required(), max(9), noWhitespace ]
}

function password() {
    return [ required(), min(8), max(50) ]
}

function passwordRe() {
    return [ required(), min(8), max(50), equalPassword ]
}

function geekusername() {
    return [ required(), min(1), max(50) ]
}